* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding: 14px;
}
.country-list {
  display: flex;
  flex-direction: column;
}

.country-item {
  display: flex;
  margin-bottom: 10px;
}

.country-flag {
  display: block;
  width: 80px;
}

.country-name {
  font-size: 16px;
  font-weight: 500;
  margin-left: 12px;
}
